<template>
    <v-row class="mt-5" justify="center">
        <v-col cols="8">
            <v-divider class="mb-5"></v-divider>
            <h2 class="mb-2">Заказы</h2>

            <v-simple-table v-if="orders.length">
                <thead>
                <tr>
                    <th>Название</th>
                    <th>План</th>
                    <th>Общая стоимость</th>
                    <th>Остаток</th>
                    <th>Дата</th>
                </tr>
                </thead>
                <tbody>
                    <tr v-for="order in orders" :key="order.id">
                        <td><RouterLink :to="`/orders/${order.id}`">{{order.name}}</RouterLink></td>
                        <td>{{order.plan ? order.plan.name : ''}}</td>
                        <td>{{order.total_price}}</td>
                        <td>{{order.remains}}</td>
                        <td>{{new Date(order.date).toLocaleDateString()}}</td>
                    </tr>
                </tbody>
            </v-simple-table>
            <p v-if="!orders.length">У пользователя пока нет заказов</p>
        </v-col>
    </v-row>
</template>

<script>
    export default {
        name: "AccountOrders",
        data: () => ({
            orders: []
        }),
        props: ['id'],
        mounted() {
            this.getOrders()
        },
        methods: {
            async getOrders() {
                const res = await this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/users/${this.id}/orders`,
                    method: 'GET'
                })
                this.orders = res.data
            },
        }
    }
</script>

