<template>
    <v-container>
        <v-row class="mt-5" justify="center">
            <v-col cols="8">
                <h2>Редактирование пользователя</h2>
                <v-form v-if="user">
                    <v-text-field
                            label="Email"
                            v-model="user.email"
                            :rules="[v => !!v || 'Обязательное поле']"
                            required
                    ></v-text-field>
                    <v-text-field
                            label="Сумма"
                            type="number"
                            v-model="user.amount"
                            required
                    ></v-text-field>
                    <v-switch v-model="user.is_active" :label="user.is_active ? 'Активен' : 'Заблокирован'"></v-switch>
                    <v-switch v-model="user.is_superuser" :label="user.is_superuser ? 'Админ' : 'Клиент'"></v-switch>
                    <v-select
                            v-model="user.site"
                            :items="sites"
                            item-text="name"
                            item-value="id"
                            label="Сайт"
                    ></v-select>
                    <v-textarea
                            v-model="user.comment"
                            label="Комментарий"
                    ></v-textarea>
                    <v-btn @click="submit" class="primary">
                        Изменить данные
                    </v-btn>
                    <v-btn @click="dialog = true" class="secondary ml-5">
                        Сделать возврат
                    </v-btn>
                    <v-btn :to="`/logs/${$route.params.id}`" class="secondary ml-5">
                        лог
                    </v-btn>
                </v-form>
            </v-col>
        </v-row>

        <AccountOrders :id="this.$route.params.id"></AccountOrders>

        <v-row class="mt-5" justify="center">
            <v-col cols="8">
                <v-divider class="mb-5"></v-divider>
                <h2 class="mb-5">Гибкие цены</h2>
                <v-btn v-if="!show_plans && !custom_prices.length" @click="getPlans(); addPlanLine()" class="primary">
                    задать гибкие цены
                </v-btn>

                <div v-if="show_plans && plans">
                    <div class="plan-flex" v-for="plan in plans_num" :key="plan">
                        <v-select
                                class="plan-input"
                                v-model="custom_prices[plan-1].plan"
                                :items="plans"
                                item-text="name"
                                item-value="id"
                                :rules="[v => !!v || 'Обязательное поле']"
                                label="План"
                        ></v-select>
                        <v-text-field
                                class="plan-input"
                                v-model="custom_prices[plan-1].min_amount"
                                type="number"
                                label="Мин. заказ"
                                :rules="[v => !!v || 'Обязательное поле']"
                                required
                        ></v-text-field>
                        <v-text-field
                                class="plan-input"
                                v-model="custom_prices[plan-1].max_amount"
                                type="number"
                                label="Макс. заказ"
                                :rules="[v => !!v || 'Обязательное поле']"
                                required
                        ></v-text-field>
                        <v-text-field
                                class="plan-input"
                                v-model="custom_prices[plan-1].price"
                                type="number"
                                label="Стоимость"
                                :rules="[v => !!v || 'Обязательное поле']"
                                required
                        ></v-text-field>
                        <v-icon class="delete-icon" @click="removePlanLine(plan-1)">mdi-delete</v-icon>
                    </div>
                    <v-btn @click="plans_num++; addPlanLine()" class="primary">
                        добавить еще
                    </v-btn>
                    <v-btn @click="savePlan" class="primary ml-5">
                        сохранить
                    </v-btn>
                </div>
            </v-col>
        </v-row>

        <v-dialog
                v-if="dialog"
                v-model="dialog"
                width="500"
        >

            <v-card>
                <v-card-title
                        class="headline grey lighten-2"
                        primary-title
                >
                    Возврат
                </v-card-title>

                <v-card-text>
                    <v-text-field
                            label="Сумма"
                            type="number"
                            v-model="refund_amount"
                            required
                    ></v-text-field>
                </v-card-text>

                <v-divider></v-divider>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                            color="primary"
                            text
                            @click="refundHandler"
                    >
                        Вернуть средства
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-container>
</template>

<script>
    import {mapActions} from 'vuex'
    import AccountOrders from './AccountOrders'

    export default {
        name: "EditAccount",
        components: {AccountOrders},
        data: () => ({
            show_plans: false,
            plans_num: 1,
            plans: null,
            custom_prices: [],
            user: null,
            sites: null,
            dialog: false,
            refund_amount: null
        }),
        mounted() {
            this.getSites()
            this.getAccount()
            this.getAccountPrices()
        },
        methods: {
            ...mapActions(['setAlert']),
            async getAccount() {
                const res = await this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/users/${this.$route.params.id}`,
                    method: 'GET'
                })
                this.user = res.data
            },
            async getSites() {
                const res = await this.axios({url: `${process.env.VUE_APP_SERVER_URL}api/v1/sites/`, method: 'GET'})
                this.sites = res.data
            },
            async getPlans() {
                const res = await this.axios({url: `${process.env.VUE_APP_SERVER_URL}api/v1/plans/`, method: 'GET'})
                this.plans = res.data
                this.show_plans = true
            },
            removePrice(id) {
                this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/plans/custom/${id}`,
                    method: 'DELETE',
                    data: {
                        prices: this.custom_prices
                    }
                }).then(() => {
                    this.setAlert({
                        active: true,
                        color: 'success',
                        text: 'Успешно удалено'
                    })
                }).catch(err => {
                    this.setAlert({
                        active: true,
                        color: 'error',
                        text: err.response.data
                    })
                })
            },
            async getAccountPrices() {
                const res = await this.axios({url: `${process.env.VUE_APP_SERVER_URL}api/v1/plans/custom/${this.$route.params.id}`, method: 'GET'})
                this.custom_prices = res.data
                if (res.data.length) {
                    this.plans_num = res.data.length
                    this.getPlans()
                    this.show_plans = true
                }
            },
            refundHandler() {
                this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/amount/${this.$route.params.id}`,
                    method: 'POST',
                    data: {
                        amount: this.refund_amount,
                    }
                }).then(res => {
                    this.setAlert({
                        active: true,
                        color: 'success',
                        text: 'Возврат прошел успешно'
                    })
                    this.user.amount = res.data.amount
                    this.dialog = false
                }).catch(err => {
                    this.setAlert({
                        active: true,
                        color: 'error',
                        text: err.response.data
                    })
                })
            },
            submit() {
                this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/users/${this.$route.params.id}`,
                    method: 'PUT',
                    data: {
                        email: this.user.email,
                        is_active: this.user.is_active,
                        is_superuser: this.user.is_superuser,
                        amount: this.user.amount,
                        site: this.user.site,
                        comment: this.user.comment
                    }
                }).then(() => {
                    this.setAlert({
                        active: true,
                        color: 'success',
                        text: 'Данные изменены'
                    })
                }).catch(err => {
                    this.setAlert({
                        active: true,
                        color: 'error',
                        text: err.response.data
                    })
                })
            },
            addPlanLine() {
                this.custom_prices.push({
                    plan: null,
                    min_amount: null,
                    max_amount: null,
                    price: null,
                    user: this.$route.params.id,
                    id: null
                })
            },
            removePlanLine(idx) {
                this.plans_num--
                if (this.custom_prices[idx].id) {
                    this.removePrice(this.custom_prices[idx].id)
                }
                this.custom_prices.splice(idx, 1)
            },
            savePlan() {
                this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/plans/custom/${this.$route.params.id}`,
                    method: 'POST',
                    data: {
                        prices: this.custom_prices
                    }
                }).then(res => {
                    this.custom_prices = res.data
                    this.setAlert({
                        active: true,
                        color: 'success',
                        text: 'Данные изменены'
                    })
                }).catch(err => {
                    this.setAlert({
                        active: true,
                        color: 'error',
                        text: err.response.data
                    })
                })
            }
        }
    }
</script>

<style scoped>
    .plan-flex {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
        align-items: center;
    }

    .plan-input {
        flex: 1 1 0;
        margin-right: 8px;
    }

    .plan-input:last-child {
        margin-right: 0;
    }

    .delete-icon {
        cursor: pointer;
        opacity: .7;
    }

    .delete-icon:hover {
        opacity: 1;
    }
</style>